<template>
    <div class="contact-form">
        <div class="row">
            <div class="columns">
                <form @submit="onSubmit">
                    <input type="text" id="name" name="name" v-model="form.name" required placeholder="Navn">
                    <select name="type" id="type" required v-model="form.type">
                        <option value="" disabled selected>Velg rolle</option>
                        <option value="forelder">Foreldre</option>
                        <option value="verge">Spiller</option>
                        <option value="spiller">Annet</option>
                    </select>
                    <input type="email" id="email" name="email" v-model="form.email" required placeholder="E-post">
                    <input type="tel" id="phone" name="phone" v-model="form.phone" required placeholder="Telefon">
                    <input type="text" id="searchmapdata" name="searchmapdata" class="gone" autocomplete="off" v-model="form.searchmapdata">
                    <input type="password" class="gone" autocomplete="off" v-model="form.cap">
                    <input type="submit" class="btn" value="Send" >
                </form>
            </div>
        </div> 
    </div>
</template>

<script>
import axios from 'axios';
const querystring = require("querystring");

export default {
    name: 'ContactForm',
    data() {
        return {
            url: process.env.VUE_APP_URL,
            sent: this.$store.state.updateFormData,
            form: {
                name: "",
                type: "",
                email: "",
                phone: "",
                searchmapdata: "",
                cap: "",
          }
        };
    },
    methods: {
      onSubmit(e) {
          e.preventDefault();
          axios
             .post(
                  this.url+'/app/mailer.php',
                  querystring.stringify(this.form)
             )
             .then(() => {
                this.$store.commit('updateFormData', true);
                this.$router.push('/sent');
             });
      },
        input() {
            if ( this.$store.state.placeAddress ) {
                this.form.searchmapdata = this.$store.state.placeAddress.description;
            }
        },
    },
    beforeMount(){
        this.input()
    },
};
</script>

