import Vue from 'vue';
import Router from 'vue-router';
import Meta from 'vue-meta';

import Start from './views/Start.vue';
import Results from './views/Results.vue';
import clubInfo from './views/clubInfo.vue';
import Contact from './views/Contact.vue';
import ContactSuccess from './views/ContactSuccess.vue';

Vue.use(Router);
Vue.use(Meta);

const router = new Router({
	mode: 'history',
	base: '/',
	routes: [
		{
			path: '/',
			name: 'start',
			component: Start,
			meta: { depth: 1 },
		},
		{
			path: '/results',
			name: 'results',
			component: Results,
			meta: { depth: 2 },
		},
		{
			path: '/club-info',
			name: 'clubInfo',
			component: clubInfo,
			meta: { depth: 3 },
			props(route) {
				return { clubid: route.query.clubid };
			},
		},
		{
			path: '/contact',
			name: 'Contact',
			component: Contact,
			meta: { depth: 4 },
		},
		{
			path: '/sent',
			name: 'Sent',
			component: ContactSuccess,
			meta: { depth: 5 },
		},

	],
});

export default router;
