<template>
  <div class="club-results">
    <div class="row">
      <div class="columns">
        <ul>
          <transition-group name="fade">
            <li 
              v-for="(club, i) in clubs" 
              :key="'club-'+i" 
            >
              <h2>{{club.name}}</h2>
              <router-link class="link" v-show="$store.state.resultData" :to="{ path: '/club-info', query: { clubid: club.id } }">G til klubbside</router-link>
            </li>
          </transition-group>
        </ul>
      </div>
    </div>
    <transition name="fade">
      <PageButton 
        v-show="clubsReady" 
        BtnTitle="Ta kontakt" 
        BtnLink="contact" 
        BtnLabel="Hjelp til  finne riktig klubb?" 
        style="transition-delay: 0.4s" />
    </transition>
  </div>
</template>

<script>
import axios from 'axios';
import PageButton from '../components/PageButton.vue'
export default {
  name: 'ClubResults',
  components: {
    PageButton
  },
  data: () => ({
    url: process.env.VUE_APP_URL,
    clubs: [],
    clubsReady: false
  }),
  mounted () {
    let lat = this.$store.state.placeCoords.lat
    let lng = this.$store.state.placeCoords.lng
    if ( this.$store.state.resultData ) {
        this.clubs = this.$store.state.resultData;
        this.clubsReady = true;
    } else {
        axios
        .get(this.url+'/app/api.php?lat='+lat+'&lng='+lng)
        // .get('//handball.test/app/api.php?lat='+lat+'&lng='+lng)
        .then(response => this.clubs = response.data.organizations[0])
        .then(() => {
          this.$store.commit('updateResultData', this.clubs);
          this.clubsReady = true;
        })
    }
  },
}
</script>

<style scoped lang="scss">
  .club-results {
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      li {
        margin-bottom: 28px;
        } 
    }
    .fade-leave-active,
    .fade-enter-active {
      transition: 0.25s;
      @for $i from 1 to 4 {
        &:nth-of-type(#{$i}) { 
          transition-delay: $i * 0.1s; 
        }
      }    
    }

    .fade-enter,
    .fade-leave-active {
      transform: translate3d(0, -20px, 0);
      opacity: 0;
    }
  }
</style>
