
<template>
  <div class="address-search">
    <div class="row">
      <div class="columns">
        <form action="">
          <input 
            id="addressSearch" 
            type="text" 
            v-model="mapsLocations" 
            v-on:click="resultsHidden = false" 
            @focus="selectInput($event)" 
            placeholder="Skriv inn adresse" 
            autocomplete="off"
          >
          <ul id="addressList" v-show="searchResults.length > 0 && mapsLocations.length && !resultsHidden">
            <li v-for="(result, i) in searchResults" :key="i">
              <a href="#" @click.prevent="selectAddress(result)">{{ result.description }}</a>
            </li>
          </ul>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddressSearch',
  data: () => ({
    api_key: process.env.VUE_APP_GMAPS_API_KEY,
    mapsLocations: '',
    searchResults: [],
    service: null,
    resultsHidden: true,
    searchTimer: null,
  }),
  metaInfo () {
    return {
      script: [{
        src: `//maps.googleapis.com/maps/api/js?key=${this.api_key}&libraries=places`,
        async: true,
        defer: true,
        callback: () => this.MapsInit()
      }]
    }
  },
  methods: {
    selectAddress (result) { 
      this.resultsHidden = true;
      this.mapsLocations = result.description; 
      this.$store.commit('updateResultData', '');
      // Fetch Coordinates from clicked data
      let _this = this;
      this.geocoder.geocode({ 
          'placeId': result.place_id
      },
      function(responses, status) {
          if (status == 'OK') {
              let lat = responses[0].geometry.location.lat();
              let lng = responses[0].geometry.location.lng();
              _this.$store.commit('updatePlaceAddress', result);
              _this.$store.commit('updatePlaceCoords', {lat,lng});
          }
      });
    },
    MapsInit () {
      this.service = new window.google.maps.places.AutocompleteService();
      this.geocoder = new window.google.maps.Geocoder();
    },
    displaySuggestions (predictions, status) {
      if (status !== window.google.maps.places.PlacesServiceStatus.OK ) {
        this.searchResults = []
        return
      }
      this.searchResults = predictions.map(prediction => prediction)
    },
    input() {
      if ( this.$store.state.placeAddress ) {
        this.mapsLocations = this.$store.state.placeAddress.description;
      }
    },
    selectInput(event) {
      if (this.$store.state.placeAddress) {
        event.target.select()
      }
    }
  },
  beforeMount(){
    this.input()
  },
  watch: {
    mapsLocations (newValue) {
      /*
      Don't send requests to api while user is typing,
      or when input has 3 characters or more.
      Wait until user has stopped typing to send request.
      This way we save money on Google Maps api's.
      */
        clearTimeout(this.searchTimer);
        this.searchTimer = setTimeout( () => {
          if (newValue && newValue !== this.$store.state.placeAddress.description ) {
            this.$store.commit('updatePlaceAddress', '');
            this.$store.commit('updatePlaceCoords', '');
            this.service.getPlacePredictions({
              input: this.mapsLocations,
              types: ["address"],
              componentRestrictions: { country: "no" }
            }, this.displaySuggestions)
          }
        }, 500);
    }
  }
}
</script>

<style scoped lang="scss">
.address-search {
  #addressList {
    list-style: none;
    padding: 28px 0 0 0;
    // border: 1px solid #ffffff;
    border-top: 0;
    margin: 0;
    // display: none;
    li {
      display: block;
      font-size: 18px;
      margin-bottom: 20px;
    }
  }
}
</style>