import Vue from 'vue';
import Vuex from 'vuex';
import VuexPersist from 'vuex-persist';

Vue.use(Vuex);

const vuexPersist = new VuexPersist({
	key: 'handball',
	storage: window.localStorage,
});

export default new Vuex.Store({
	plugins: [vuexPersist.plugin],
	state: {
		placeAddress: '',
		placeCoords: '',
		resultData: '',
		formData: false,
	},
	actions: {
		updatePlaceAddress({ commit }, place) {
			commit('updatePlaceAddress', place);
		},
		updatePlaceCoords({ commit }, coords) {
			commit('updatecoordsCoords', coords);
		},
		updateResultData({ commit }, result) {
			commit('updateResultData', result);
		},
		updateFormData({ commit }, sent) {
			commit('updateFormData', sent);
		},
	},
	mutations: {
		updatePlaceAddress(state, place) {
			state.placeAddress = place;
		},
		updatePlaceCoords(state, coords) {
			state.placeCoords = coords;
		},
		updateResultData(state, result) {
			state.resultData = result;
		},
		updateFormData(state, sent) {
			state.formData = sent;
		},
	},
});
